	module.exports = {"data":{"markdownRemark":{"html":"<h1>find</h1>\n<h2>Массовые операции над файлами</h2>\n<p>Пример - поиск функции во всех .so файлах, генерируемых при билде:</p>\n<div class=\"gatsby-highlight\" data-language=\"shell\"><pre class=\"language-shell\"><code class=\"language-shell\"><span class=\"token function\">find</span> ./build -name <span class=\"token string\">\"*.so\"</span> -exec <span class=\"token function\">sh</span> -c <span class=\"token string\">'nm {} | grep gst_app_src_push_buffer &amp;&amp; echo {}'</span> <span class=\"token punctuation\">\\</span><span class=\"token punctuation\">;</span></code></pre></div>\n<h2>Массовый реплэйс в файлах</h2>\n<div class=\"gatsby-highlight\" data-language=\"shell\"><pre class=\"language-shell\"><code class=\"language-shell\"><span class=\"token function\">find</span> <span class=\"token builtin class-name\">.</span> -name <span class=\"token string\">\"*.js\"</span> -exec <span class=\"token function\">sed</span> -r -i -- <span class=\"token string\">\"s/\\.\\.\\/(Ok|Cancel|Dropdown)Button/..\\/..\\/common\\/components\\/<span class=\"token entity\" title=\"\\1\">\\1</span>Button/g\"</span> <span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span> <span class=\"token punctuation\">\\</span><span class=\"token punctuation\">;</span></code></pre></div>\n<p>Ищет во всех .js файлах и заменяет:\n<code class=\"language-text\">../OkButton</code> на <code class=\"language-text\">../../common/components/OkButton</code>\n<code class=\"language-text\">../CancelButton</code> на <code class=\"language-text\">../../common/components/CancelButton</code>\n<code class=\"language-text\">../DropdownButton</code> на <code class=\"language-text\">../../common/components/DropdownButton</code></p>","frontmatter":{"path":"/blog/find","title":"find"}}},"pathContext":{}}

